<template>
  <div class="mark-down">
    <MarkdownPreview v-if="markdown" :initialValue="markdown" />
    <div id="markdown-gitalk"></div>
  </div>
</template>

<script>
import { MarkdownPreview } from "vue-meditor";
export default {
  components: {
    MarkdownPreview
  },
  data() {
    return {
      markdown: null,
      gitalk: null
    };
  },
  watch: {
    "$route.params": {
      handler({ type, url, key }) {
        let value;
        if (type) {
          switch (type) {
            case "@/share":
              value = require(`@/share/${url}`);
              break;
          }
        }
        this.markdown = value;
        // key 用来生成 gitalk
        if (key) {
          this.$nextTick(() => {
            this.gitalk = new Gitalk({
              clientID: "Iv1.9ec4920589d90b85",
              clientSecret: "8a47e1930a5c1ec02199472fae4ca14655a8aa43",
              repo: "Gitalk",
              owner: "z7xisoo",
              admin: ["z7xisoo"],
              id: key,
              distractionFreeMode: false // Facebook-like distraction free mode
            });
            this.gitalk.render("markdown-gitalk");
          });
        }
      },
      immediate: true
    }
  },
  mounted() {},
  beforeDestroy() {
    this.markdown = null;
  }
};
</script>

<style lang="scss" scoped>
.mark-down {
  width: 100%;
  height: 100%;
}
</style>